.container {
    display: flex;
    position: relative;
    justify-content: center;
    height: 100vh;
}

.textos {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    margin-top: 10%;
    min-width: 25%;
}

.textos p {
    background-color: white;
    padding: 1em;
    margin-bottom: 10em;
    z-index: 1;
    position: relative;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
}

.h1 {
    width: fit-content;
    margin: 0 auto; 
}

.img {
    max-width: 50vh; 
    z-index: 0;
    position: relative;
    margin-top: 2vh;
    margin-left: -4em ;
    min-width: 25%
}

.img img {
    max-width: 100%;
    height: auto; 
}

@media (max-width: 768px) {
    .container {
        align-items: center;
        margin-top: 4em;
        margin-left: 1em;
    }

    .textos p {
        width: 20em;
        margin-bottom: 22em;
    }

    .textos h1 {
        background-color: white;
        font-size: 15px;
        padding-top: 1em;
        width: fit-content;
        padding-right: 1em;
    }

    .img {
        max-width: 50vh; 
        z-index: 0;
        position: relative;
        margin-right: 1em;
    }
}